.recipe-favorite-list-item{
    display: flex;
    align-items: center;
    gap: 8px;
}

.recipe-favorite-list-item-profile-box{
    width: 32px;
    height: 32px;
}
.recipe-favorite-list-item-profile-image{
    border-radius: 50% 50%;
    width: 100%;
    height: 100%;

    background-position: 50% 50%;
    background-size: 100% 100%;
}
.recipe-favorite-list-item-nickname{
    color: rgba(0, 0, 0, 0.7);
    
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
}