#user-top-wrapper {
  padding: 40px 0px 45px;

  display: flex;
  justify-content: center;
}

.user-top-container {
  width: 1200px;
  min-width: 1200px;

  display: flex;
  align-items: center;
  gap: 25px;
}

.user-top-my-profile-image-box {
  border-radius: 50%;

  width: 120px;
  height: 120px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(217, 217, 217, 1);

  cursor: pointer;
}

.user-top-profile-image {
  border-radius: 50%;
  width: 100%;
  height: 100%;

  background-position: 50% 50%;
  background-size: 100% 100%;
}

.user-top-profile-image-box {
  border-radius: 50%;

  width: 120px;
  height: 120px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(217, 217, 217, 1);

  background-position: 50% 50%;
  background-size: 100% 100%;
}

.user-top-info-box {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.user-top-info-nickname-box {
  display: flex;
  align-items: center;
  gap: 10px;
}

.user-top-info-nickname-input {
  border: none;
  outline: none;
  background: none;
  padding: 0;

  padding-block: 0px;
  padding-inline: 0px;

  color: rgba(0, 0, 0, 1);

  font-size: 24px;
  font-weight: 500;
}

.user-top-info-nickname {
  color: rgba(0, 0, 0, 1);

  font-size: 24px;
  font-weight: 500;
  line-height: 140%;
}

.user-top-info-email {
  color: rgba(0, 0, 0, 0.55);

  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
}

#user-bottom-wrapper {
  padding: 40px 0px;
  min-height: 880px;

  display: flex;
  justify-content: center;

  background-color: rgba(0, 0, 0, 0.05);
}

.user-bottom-container {
  width: 1200px;
  min-width: 1200px;

  display: flex;
  flex-direction: column;

  gap: 20px;
}

.user-bottom-title {
  color: rgba(0, 0, 0, 1);

  font-size: 24px;
  font-weight: 500;
  line-height: 140%;
}

.user-bottom-contents-box {
  width: 100%;

  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 24px;
}

.user-bottom-contents {
  grid-column: 1 / 2;

  display: flex;
  flex-direction: column;
  gap: 16px;
}

.user-bottom-contents-nothing {
  width: 100%;
  height: 76px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: rgba(0, 0, 0, 0.4);

  font-size: 24px;
  font-weight: 500;
  line-height: 140%;
}

.user-bottom-side-box {
  grid-column: 2 / 3;
}

.user-bottom-side-card {
  width: 100%;
  height: 76px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(255, 255, 255, 1);

  cursor: pointer;
}

.user-bottom-side-card:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.user-bottom-side-container {
  display: flex;
  align-items: center;

  gap: 6px;
}

.user-bottom-side-text{
  color: rgba(0, 0, 0, 1);

  font-size: 18px;
  font-weight: 500;
  line-height: 140%;
}

.user-bottom-pagination-box{
  margin-top: 60px;

  display: flex;
  justify-content: center;
}

.user-bottom-selector {
  display: flex;
  justify-content: center; /* 가운데 정렬 */
  gap: 30px; /* 버튼 사이 간격 */
  margin-bottom: 20px; /* 아래 여백 */
}

.user-bottom-selector button {
  padding: 10px 20px; /* 버튼 내부 패딩 */
  font-size: 24px; /* 폰트 크기 */
  border: none;
  border-radius: 43px;
  background-color: transparent;
  color: black;
  cursor: pointer;
  transition: all 0.3s ease; /* 부드러운 전환 효과 */
}

.user-bottom-selector button.selected {
  color: black; /* 선택된 버튼의 글자색 */
  background-color: rgba(0, 123, 255, 1); /* 선택된 버튼의 배경색 */
}

.user-bottom-selector button:hover {
  background-color: rgba(0, 123, 255, 0.4); /* 마우스 호버 시 배경색 */
}
